.page {
  position: relative;
  min-width: 320px;
  min-height: 100%;

  margin: 0;
  padding: 0;
  font-family: $font;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: $c-gray-750;

  scroll-behavior: smooth;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
