.item {
  display: grid;
  grid-template-columns: auto;
  transition: $transition;

  @media (min-width: 600px) {
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
  }
  @media (min-width: 900px) {
    grid-template-columns: auto 3fr 300px;
  }

  &__picture {
    max-width: 100%;

    margin: 0 auto 22px;
    transition: $transition;

    @media (min-width: $mobile) {
      padding: 0 38px;
    }

    @media (min-width: $tablet) {
      margin-bottom: 0;
      padding-top: 8px;
    }
    @media (min-width: $large) {
      margin: 0;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    margin-bottom: 13px;

    @media (min-width: 600px) {
      padding-left: 40px;
      margin-bottom: 15px;
    }

    @media (min-width: 900px) {
      margin-bottom: 0;
    }
  }

  &__title {
    margin: 0;
    padding-bottom: 17px;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 28px;

    @media (min-width: $mobile) {
      font-size: 24px;
      line-height: 16px;
    }

    @media (min-width: $tablet) {
      font-size: 24px;
      line-height: 36px;
      padding-bottom: 8px;
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;

    @media (min-width: $mobile) {
      padding-right: 20%;
    }

    & li {
      display: flex;
      flex-direction: column;
      width: calc(100% / 2);
      max-width: 140px;
      color: $c-gray-400;
      padding-bottom: 12px;
      transition: $transition;

      @media (min-width: $tablet) {
        width: calc(100% / 2);
      }

      @media (min-width: 1024px) {
        width: calc(100% / 3);
      }
    }

    & span {
      font-family: $font-alt;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: $c-gray-750;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    padding-top: 7px;
  }

  &__tag {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 8px;
    padding: 8px 11px;
    font-size: 12px;
    line-height: 16px;

    color: $c-green;
    background-color: #f2faf0;
    border-radius: 22px;

    &:not(:last-child) {
      margin-right: 16px;

      @media (min-width: $mobile) {
        margin-right: 20px;
      }

      @media (min-width: $tablet) {
        margin-right: 13px;
      }
    }

    @media (min-width: $tablet) {
      padding: 8px 16px;
    }

    @media (min-width: $large) {
      padding: 8px 20px 8px 11px;
    }

    &--red {
      color: $c-red-500;
      background-color: #faf0f0;

      & svg {
        fill: $c-red-500;
      }
    }

    &-icon {
      width: 16px;
      height: 16px;
      margin-right: 10px;
    }
  }

  &__price {
    position: relative;
    display: flex;
    flex-direction: column;

    grid-column: 1/2;

    font-size: 14px;
    line-height: 20px;
    color: $c-gray-400;
    transition: $transition;

    padding-top: 18px;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #f0eeeb;
    }

    @media (min-width: 600px) {
      grid-column: 1/3;
    }

    @media (min-width: $mobile) {
      padding-top: 26px;
    }

    @media (min-width: 900px) {
      padding-top: 0;
      padding-left: 31px;

      &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 1px;
        height: 100%;
        background: #f0eeeb;
      }
    }

    @media (min-width: 900px) {
      grid-column: 3/4;
    }
  }

  &__reserve {
    padding-bottom: 12px;

    @media (min-width: 900px) {
      padding-right: 30px;
    }

    &-date {
      font-family: $font-alt;
      font-weight: 500;
      color: $c-blue-500;
    }

    &-price {
      font-weight: bold;
      font-size: 20px;
      line-height: 28px;
      color: $c-gray-750;

      @media (min-width: $mobile) {
        font-size: 24px;
        line-height: 36px;
      }
    }
  }

  &__dev {
    padding-right: 50px;
    padding-bottom: 17px;

    @media (min-width: $mobile) {
      padding-bottom: 25px;
      max-width: 250px;
    }

    &-price {
      font-family: $font-alt;
      font-weight: 500;
      color: $c-red-500;
    }
  }

  &__price-btn {
    margin-top: auto;

    @media (min-width: $large) {
      margin-top: 0;
    }
  }
}
