@font-face {
  font-family: "Roboto", "Arial", sans-serif;
  font-display: auto;
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-display: auto;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

html {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0; }

a {
  text-decoration: none; }

img {
  height: auto;
  max-width: 100%;
  vertical-align: top; }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  position: relative;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: #383736;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.btn {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #383736;
  background-color: #fac62d;
  border-radius: 6px;
  text-align: center;
  transition: 0.5s; }
  .btn:focus {
    outline: none; }
  .btn:hover {
    background: #f0b505; }
  .btn:active {
    background-color: #e1ab08; }
  .btn__icon {
    width: 16px;
    height: 16px;
    margin-right: 10px; }

.item {
  display: grid;
  grid-template-columns: auto;
  transition: 0.5s; }
  @media (min-width: 600px) {
    .item {
      grid-template-columns: auto auto;
      grid-template-rows: auto auto; } }
  @media (min-width: 900px) {
    .item {
      grid-template-columns: auto 3fr 300px; } }
  .item__picture {
    max-width: 100%;
    margin: 0 auto 22px;
    transition: 0.5s; }
    @media (min-width: 768px) {
      .item__picture {
        padding: 0 38px; } }
    @media (min-width: 920px) {
      .item__picture {
        margin-bottom: 0;
        padding-top: 8px; } }
    @media (min-width: 1200px) {
      .item__picture {
        margin: 0; } }
  .item__info {
    display: flex;
    flex-direction: column;
    margin-bottom: 13px; }
    @media (min-width: 600px) {
      .item__info {
        padding-left: 40px;
        margin-bottom: 15px; } }
    @media (min-width: 900px) {
      .item__info {
        margin-bottom: 0; } }
  .item__title {
    margin: 0;
    padding-bottom: 17px;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 28px; }
    @media (min-width: 768px) {
      .item__title {
        font-size: 24px;
        line-height: 16px; } }
    @media (min-width: 920px) {
      .item__title {
        font-size: 24px;
        line-height: 36px;
        padding-bottom: 8px; } }
  .item__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap; }
    @media (min-width: 768px) {
      .item__list {
        padding-right: 20%; } }
    .item__list li {
      display: flex;
      flex-direction: column;
      width: calc(100% / 2);
      max-width: 140px;
      color: #989691;
      padding-bottom: 12px;
      transition: 0.5s; }
      @media (min-width: 920px) {
        .item__list li {
          width: calc(100% / 2); } }
      @media (min-width: 1024px) {
        .item__list li {
          width: calc(100% / 3); } }
    .item__list span {
      font-family: "Roboto Medium", "Arial", sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #383736; }
  .item__tags {
    display: flex;
    flex-wrap: wrap;
    padding-top: 7px; }
  .item__tag {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 8px;
    padding: 8px 11px;
    font-size: 12px;
    line-height: 16px;
    color: #64b851;
    background-color: #f2faf0;
    border-radius: 22px; }
    .item__tag:not(:last-child) {
      margin-right: 16px; }
      @media (min-width: 768px) {
        .item__tag:not(:last-child) {
          margin-right: 20px; } }
      @media (min-width: 920px) {
        .item__tag:not(:last-child) {
          margin-right: 13px; } }
    @media (min-width: 920px) {
      .item__tag {
        padding: 8px 16px; } }
    @media (min-width: 1200px) {
      .item__tag {
        padding: 8px 20px 8px 11px; } }
    .item__tag--red {
      color: #eb4b51;
      background-color: #faf0f0; }
      .item__tag--red svg {
        fill: #eb4b51; }
    .item__tag-icon {
      width: 16px;
      height: 16px;
      margin-right: 10px; }
  .item__price {
    position: relative;
    display: flex;
    flex-direction: column;
    grid-column: 1/2;
    font-size: 14px;
    line-height: 20px;
    color: #989691;
    transition: 0.5s;
    padding-top: 18px; }
    .item__price::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #f0eeeb; }
    @media (min-width: 600px) {
      .item__price {
        grid-column: 1/3; } }
    @media (min-width: 768px) {
      .item__price {
        padding-top: 26px; } }
    @media (min-width: 900px) {
      .item__price {
        padding-top: 0;
        padding-left: 31px; }
        .item__price::before {
          position: absolute;
          content: "";
          top: 0;
          left: 0;
          width: 1px;
          height: 100%;
          background: #f0eeeb; } }
    @media (min-width: 900px) {
      .item__price {
        grid-column: 3/4; } }
  .item__reserve {
    padding-bottom: 12px; }
    @media (min-width: 900px) {
      .item__reserve {
        padding-right: 30px; } }
    .item__reserve-date {
      font-family: "Roboto Medium", "Arial", sans-serif;
      font-weight: 500;
      color: #2794DB; }
    .item__reserve-price {
      font-weight: bold;
      font-size: 20px;
      line-height: 28px;
      color: #383736; }
      @media (min-width: 768px) {
        .item__reserve-price {
          font-size: 24px;
          line-height: 36px; } }
  .item__dev {
    padding-right: 50px;
    padding-bottom: 17px; }
    @media (min-width: 768px) {
      .item__dev {
        padding-bottom: 25px;
        max-width: 250px; } }
    .item__dev-price {
      font-family: "Roboto Medium", "Arial", sans-serif;
      font-weight: 500;
      color: #eb4b51; }
  .item__price-btn {
    margin-top: auto; }
    @media (min-width: 1200px) {
      .item__price-btn {
        margin-top: 0; } }

.popup {
  padding: 40px 8px;
  background-color: #e5e5e5;
  width: 100%;
  height: 100%;
  min-height: 100vh; }
  @media (min-width: 600px) {
    .popup {
      padding: 40px 28px; } }
  .popup__inner {
    position: relative;
    padding: 26px 20px 20px;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #ffffff;
    box-shadow: 0px 2px 24px rgba(0, 0, 0, 0.04);
    border-radius: 8px; }
    @media (min-width: 768px) {
      .popup__inner {
        padding: 34px 32px 24px; } }
    @media (min-width: 920px) {
      .popup__inner {
        padding: 25px 32px 24px; } }
    @media (min-width: 1200px) {
      .popup__inner {
        padding-bottom: 15px; } }
  .popup__close {
    position: absolute;
    top: 16px;
    right: 16px;
    padding: 0;
    margin: 0;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    width: 24px;
    height: 24px;
    transition: 0.5s; }
    .popup__close:focus {
      outline: none; }
    .popup__close:hover {
      background-color: #f5f4f2;
      border-radius: 4px; }
      .popup__close:hover svg {
        fill: #383736; }
    .popup__close:active {
      background-color: #e7e4de; }
      .popup__close:active svg {
        fill: #383736; }
    .popup__close svg {
      fill: #c4c4c4;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
