.popup {
  padding: 40px 8px;
  background-color: #e5e5e5;
  width: 100%;
  height: 100%;
  min-height: 100vh;

  @media (min-width: 600px) {
    padding: 40px 28px;
  }

  &__inner {
    position: relative;
    padding: 26px 20px 20px;
    max-width: $large;
    margin: 0 auto;
    background-color: #ffffff;
    box-shadow: 0px 2px 24px rgba(0, 0, 0, 0.04);
    border-radius: 8px;

    @media (min-width: 768px) {
      padding: 34px 32px 24px;
    }
    @media (min-width: 920px) {
      padding: 25px 32px 24px;
    }

    @media (min-width: 1200px) {
      padding-bottom: 15px;
    }
  }

  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    @include btn-reset;

    width: 24px;
    height: 24px;

    transition: $transition;

    &:hover {
      background-color: #f5f4f2;
      border-radius: 4px;

      & svg {
        fill: $c-gray-750;
      }
    }

    &:active {
      background-color: #e7e4de;

      & svg {
        fill: $c-gray-750;
      }
    }

    & svg {
      fill: #c4c4c4;
      @include center;
    }
  }
}
