.btn {
  @include btn-reset;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  font-family: $font-alt;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #383736;
  background-color: #fac62d;
  border-radius: 6px;
  text-align: center;
  transition: 0.5s;

  &:hover {
    background: #f0b505;
  }

  &:active {
    background-color: #e1ab08;
  }

  &__icon {
    width: 16px;
    height: 16px;
    margin-right: 10px;
  }
}
